export default {
  features (state) {
    return state.features
  },
  featuresAlert (state) {
    return state.featuresAlert
  },
  mainFeatures (state) {
    const main = ['Mensagens', 'Reservas', 'Notcias', 'Mudanas', 'Enquetes', 'Pessoas']
    return state.features.filter(elem => main.indexOf(elem.title) !== -1)
  },
  area (state) {
    return state.features.find(elem => elem.title === 'rea comum' || elem.title === 'rea privativa') || false
  },
  assistis (state) {
    return false
    // return state.features.find(elem => elem.title === 'Convenincias') || false
  }
}



